import { default as url }    from './utils/url'
import { default as fetch }  from './utils/fetch'

export {
  /** Array utility. */
  array,
  /** Check utility. */
  check,
  /** DOM utility. */
  dom,
  /** Data utility. */
  data,
  /** Device utility. */
  device,
  /** Function utility. */
  fn,
  /** String utility. */
  string,
  /** Object utility. */
  object,
  /** URL utility. */
  url,
  /** Fetch utility. */
  fetch
}



